import React from 'react';
import clip from '../assets/img/clip.png';
import blurred from '../assets/img/blurred.jpg';
import Col from 'react-bootstrap/Col';

export default class PhotoCard extends React.Component {

    render() {
        const { i, tilt, showUploadModal, name } = this.props;
        return (
            <Col className="photo-card-wrapper" xs={2}>
                <img alt="" className="clip" src={clip} />
                <div className={`photo-card ${tilt}`}>
                    <div className="photo">
                        <img alt="" src={blurred} />
                    </div>
                    <div className="greetings">
                        {i===0 && <button onClick={showUploadModal}>Upload Yours</button>}
                        {!name && i!==0 && <h4>{i%2===0 ? "John" : "Jane"} Doe</h4>}
                        {<h4>{name}</h4>}
                    </div>
                </div>
            </Col>
        );
    }
}