import React from 'react';
import '../assets/styles/lightbulb.scss';

export default class LightBulb extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            isOn: false
        }
    }

    componentDidMount = () => {
        setInterval(() => this.setState({isOn: this.randomInteger(2) === 1}), 800);
    }

    randomInteger = (range) => {
        return parseInt(Math.random() * range);
    }

    render() {
        const {
            margin
        } = this.props;
        const lightStateClassName = this.state.isOn ? 'on' : 'off';
        const classNames = `bulb ${lightStateClassName}`;
        return (
            <li style={{marginTop: margin}} className={classNames}></li>
        );
    }  
};