import React from 'react';
import Row from 'react-bootstrap/Row';
import ProgressBar from 'react-bootstrap/ProgressBar';
import LightBulb from './LightBulb';
import FileUploader from './FileUploader';
import PhotoCard from './PhotoCard';
import tick from '../assets/img/tick.png';
import Modal from 'react-bootstrap/Modal';

const tilts = ["tilt-left", "tilt-right", "tilt-left extra", "tilt-right extra", "tilt-left slight", "tilt-right slight"];

export default class PhotoHanger extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            n: 40,
            tilts: [],
            showUploadModal: false,
            showUploadingModal: false,
            uploaded: false,
            names: []
        }
        this.setTilts();
        this.loadWishes();
    }

    loadWishes = () => {
        fetch('http://http://ec2-34-217-133-243.us-west-2.compute.amazonaws.com:3000/api/wishes', {
            method: 'GET'
        }).then((response) => {
            return response.json();
          })
          .then((response) => {
            const names = response.map((e) => e.name);
            names.unshift("");
            this.setState({
                names: names
            });
          }).catch(err => {});
    }

    showUploadModal = () => {
        this.setState({showUploadModal: true});
    }

    setTilts = () => {
        const _tilts = [...Array(this.state.n).keys()].map(() => tilts[this.randomInteger(6)]);
        setTimeout(() => {
            this.setState({tilts: _tilts});
        }, 200);
    }

    handleClose = (data) => {
        if(!data || !data.file || !data.greetings) return;
        if(!data.name) data.name = "Anonymous";
        let formData = new FormData();
        formData.append('image', data.file);
        formData.append('name', data.name);
        formData.append('greetings', data.greetings);
        this.setState({showUploadingModal: true, showUploadModal: false, uploaded: false});
        fetch('http://http://ec2-34-217-133-243.us-west-2.compute.amazonaws.com:3000/api/upload', {
            method: 'POST',
            body: formData
        }).then((response) => {
            return response.json();
          })
          .then((response) => {
            console.log(response);
            if(response.name) {
                const names = this.state.names;
                // names.splice(1, 0, response.name);
                names.push(response.name);
                this.setState({uploaded: true, names: names});
            }
          }).catch(err => {
        });
    }

    randomInteger = (range) => {
        return parseInt(Math.random() * range);
    }

    close = () => {
        this.setState({
            showUploadingModal: false
        })
    }

    render(){
        const {tilts, names, n, showUploadModal} = this.state;
        console.log(names);
        return (  
          <Row className="photo-hanger">
                {[...Array(this.state.n).keys()].map((i) => {
                    return (
                        <React.Fragment key={i}>
                            <PhotoCard i={i} name={names[i]} tilt={tilts[i]} showUploadModal={this.showUploadModal}/>
                            {
                                i!==n-1 && (i+1)%5!==0 && (
                                    <LightBulb margin={60}/>
                                )
                            }
                        </React.Fragment>
                    );
                })}
                <FileUploader showModal={showUploadModal} handleClose={this.handleClose}></FileUploader>
                <Modal
                    size="lg"
                    centered
                    show={this.state.showUploadingModal}
                    onHide={this.close}>
                    <Modal.Header closeButton>
                    <Modal.Title>
                        {this.state.uploaded ? "Saved!" : "Uploading your Wish.."}
                    </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                       {!this.state.uploaded && (
                            <div>
                                <h3>Your Wish is being saved, please wait..</h3>
                                <ProgressBar animated now={100} striped variant="success" />
                            </div>
                       )}
                       {this.state.uploaded && (
                            <div style={{display: "flex"}}>
                                <img src={tick} height="40" width="40" alt="" style={{marginRight: 20}}/>
                                <span>
                                    <h3>Your Wish was saved successfully!</h3>
                                    <h4>I'll display it here, when it's time!</h4>    
                                </span>
                            </div>
                       )}
                    </Modal.Body>
                </Modal>
            </Row>
        );
    }  
};