import React from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import Dropzone from 'react-dropzone';
import tick from '../assets/img/tick.png';
import Tooltip from 'react-bootstrap/Tooltip'
import OverlayTrigger from 'react-bootstrap/OverlayTrigger'

export default class FileUploader extends React.Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            file: null,
            greetings: "",
            name: ""
        }
        this.saveButton = React.createRef();
    }

    fileUploadIcon = () => {
        return <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="file-upload" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path fill="currentColor" d="M224 136V0H24C10.7 0 0 10.7 0 24v464c0 13.3 10.7 24 24 24h336c13.3 0 24-10.7 24-24V160H248c-13.2 0-24-10.8-24-24zm65.18 216.01H224v80c0 8.84-7.16 16-16 16h-32c-8.84 0-16-7.16-16-16v-80H94.82c-14.28 0-21.41-17.29-11.27-27.36l96.42-95.7c6.65-6.61 17.39-6.61 24.04 0l96.42 95.7c10.15 10.07 3.03 27.36-11.25 27.36zM377 105L279.1 7c-4.5-4.5-10.6-7-17-7H256v128h128v-6.1c0-6.3-2.5-12.4-7-16.9z"></path></svg>
    }

    dayDiff = () => {
        const bDay = new Date(2019, 8, 8);
        const today = new Date();
        today.setHours(0,0,0,0);
        return Math.round((bDay.getTime() - today)/(24*60*60*1000));
    }

    onUpload = (files) => {
        if(files.length === 0) return;
        this.setState({file: files[0]});
    }

    save = () => {
        if(!this.state.file || !this.state.greetings) return;
        this.close(this.state);
    }

    close = (data) => {
        this.props.handleClose(data);
        // this.setState({
        //     file: null,
        //     greetings: "",
        //     name: ""
        // });
    }

    render() {
        const { showModal } = this.props;
        return (
            <Modal
                size="lg"
                centered
                show={showModal}
                onHide={this.close}>
                <Modal.Header closeButton>
                <Modal.Title>
                    Wish Asmita Happy B'day! ({this.dayDiff() > 0 ? `${this.dayDiff()} days to go!` : (this.dayDiff() === 0 ? "It's today!" : "Sorry mate, you're late to the party :/")})
                </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Dropzone onDrop={this.onUpload} multiple={false} accept="image/*">
                        {({getRootProps, getInputProps, isDragActive}) => (
                            <section>
                                <div {...getRootProps()}>
                                    <input {...getInputProps()} />
                                    <div className={`drag ${isDragActive ? 'active' : ''}`}>
                                        {!this.state.file && (
                                            <React.Fragment>
                                                <div className="drag-icon">
                                                    {this.fileUploadIcon()}
                                                </div>
                                                <p>
                                                    {isDragActive ? "Drop to upload" : <span><strong>Click here</strong> <small>or</small> <strong>Drag 'n' Drop to upload</strong></span>}
                                                    <br></br>
                                                    <small>You can upload a photo of Asmita (with you?)<br></br>or something unique! Imagination is the key :)</small>
                                                </p>
                                            </React.Fragment>
                                        )}
                                        {this.state.file && (
                                            <div style={{fontSize: 30}}>
                                                <img src={tick} height="40" width="40" alt="" style={{marginRight: 20}}/>
                                                Selected file: {this.state.file.name}
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </section>
                        )}
                    </Dropzone>
                    <InputGroup style={{marginTop: 40}}>
                        <InputGroup.Prepend>
                        <InputGroup.Text>Your name?</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                            style={{height: 58}}
                            aria-label="Your name?"
                            value={this.state.name}
                            onChange={(e)=>this.setState({name: e.target.value})}
                            placeholder="I know you're not Jon Snow :)" />
                    </InputGroup>
                    <InputGroup style={{marginTop: 20}}>
                        <InputGroup.Prepend>
                        <InputGroup.Text>Add your message here:</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                            as="textarea"
                            aria-label="Add your message here:"
                            rows="4"
                            value={this.state.greetings}
                            onChange={(e)=>this.setState({greetings: e.target.value})}
                            placeholder={
                                `You can keep it as simple as "Happy b'day!" or you can just describe the photo. Try to keep it short and sweet, though you're welcome to write as much as you want!`
                            } />
                    </InputGroup>
                </Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={() => this.close()}>
                    Cancel :(
                </Button>
                <OverlayTrigger
                    placement="top"
                    overlay={
                        <Tooltip>
                            {(!this.state.file || !this.state.greetings) && (
                                <span>
                                    What's the hurry?{!this.state.file ? " Why not upload a photo first? :)"  : " Why not type something first? :)"}
                                </span>
                            )}
                            {this.state.file && this.state.greetings && "Good to go!"}
                        </Tooltip>
                    }
                    >
                    <Button ref={this.saveButton} variant="primary" onClick={this.save}>
                        Upload
                    </Button>
                </OverlayTrigger>
                </Modal.Footer>
            </Modal>
        );
    }
}